import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Paper,
  Button,
  TextField,
  Grid,
  Card,
  CardContent,
  Divider,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  SelectChangeEvent,
  Snackbar,
  Alert,
  IconButton,
  Tooltip,
  useTheme
} from '@mui/material';
import SaveIcon from '@mui/icons-material/Save';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import AddLocationIcon from '@mui/icons-material/AddLocation';
import LayersIcon from '@mui/icons-material/Layers';
import SettingsIcon from '@mui/icons-material/Settings';
import PreviewIcon from '@mui/icons-material/Preview';

// Define types for our project and map settings
interface MarkerCategory {
  id: number;
  name: string;
  color: string;
  icon: string;
}

interface MapSettings {
  initialZoom: number;
  initialCenter: {
    lat: number;
    lng: number;
  };
  baseMapStyle: string;
  allowComments: boolean;
  allowVoting: boolean;
  markerCategories: MarkerCategory[];
}

interface Project {
  id: number;
  title: string;
  description: string;
  type: string;
  createdAt: string;
  status: string;
  mapSettings: MapSettings;
}

// Type for settings that can be toggled
type ToggleableSetting = 'allowComments' | 'allowVoting';

// Mock project data
const getMockProject = (id: string): Project => {
  return {
    id: parseInt(id),
    title: 'Downtown Revitalization',
    description: 'Community input for downtown area improvements',
    type: 'map',
    createdAt: '2023-02-15',
    status: 'active',
    mapSettings: {
      initialZoom: 13,
      initialCenter: { lat: 40.7128, lng: -74.0060 }, // New York City
      baseMapStyle: 'streets',
      allowComments: true,
      allowVoting: true,
      markerCategories: [
        { id: 1, name: 'Infrastructure', color: '#FF5733', icon: '' },
        { id: 2, name: 'Green Space', color: '#33FF57', icon: '' },
        { id: 3, name: 'Safety Concern', color: '#FF3357', icon: '' },
        { id: 4, name: 'Community Facility', color: '#3357FF', icon: '' }
      ]
    }
  };
};

const MapEditorPage = () => {
  const { projectId } = useParams<{ projectId: string }>();
  const navigate = useNavigate();
  const theme = useTheme();
  
  const [project, setProject] = useState<Project | null>(null);
  const [activeTab, setActiveTab] = useState('map');
  const [mapSettings, setMapSettings] = useState<MapSettings | null>(null);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success' as 'success' | 'error' | 'info' | 'warning'
  });

  useEffect(() => {
    if (projectId) {
      // In a real app, this would be an API call
      const mockProject = getMockProject(projectId);
      setProject(mockProject);
      setMapSettings(mockProject.mapSettings);
    }
  }, [projectId]);

  const handleSaveMap = () => {
    // In a real app, this would save to a database
    if (project && mapSettings) {
      setProject({ ...project, mapSettings });
      setSnackbar({
        open: true,
        message: 'Map settings saved successfully!',
        severity: 'success'
      });
    }
  };

  const handleSettingChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    if (name === 'initialZoom') {
      setMapSettings(prev => prev ? { ...prev, initialZoom: Number(value) } : null);
    }
  };

  const handleBaseMapChange = (e: SelectChangeEvent) => {
    setMapSettings(prev => prev ? { ...prev, baseMapStyle: e.target.value } : null);
  };

  const handleToggleChange = (setting: ToggleableSetting) => {
    setMapSettings(prev => {
      if (!prev) return null;
      return {
        ...prev,
        [setting]: !prev[setting]
      };
    });
  };

  const handleCloseSnackbar = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  if (!project || !mapSettings) {
    return (
      <Container maxWidth="lg">
        <Box sx={{ py: 4, textAlign: 'center' }}>
          <Typography variant="h5">Loading project...</Typography>
        </Box>
      </Container>
    );
  }

  return (
    <Box sx={{ flexGrow: 1, py: 4 }}>
      <Container maxWidth="lg">
        <Paper sx={{ p: 3, mb: 4 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <IconButton 
                sx={{ mr: 1 }} 
                onClick={() => navigate('/admin')}
                aria-label="Back to admin"
              >
                <ArrowBackIcon />
              </IconButton>
              <Typography variant="h4" component="h1">
                Map Editor: {project.title}
              </Typography>
            </Box>
            <Button 
              variant="contained" 
              startIcon={<SaveIcon />}
              onClick={handleSaveMap}
            >
              Save Map
            </Button>
          </Box>

          <Box sx={{ mb: 3 }}>
            <Typography variant="body1" paragraph>
              {project.description}
            </Typography>
          </Box>

          <Box sx={{ mb: 3, display: 'flex', gap: 1 }}>
            <Button 
              variant={activeTab === 'map' ? 'contained' : 'outlined'} 
              startIcon={<LayersIcon />}
              onClick={() => setActiveTab('map')}
            >
              Map View
            </Button>
            <Button 
              variant={activeTab === 'markers' ? 'contained' : 'outlined'} 
              startIcon={<AddLocationIcon />}
              onClick={() => setActiveTab('markers')}
            >
              Markers
            </Button>
            <Button 
              variant={activeTab === 'settings' ? 'contained' : 'outlined'} 
              startIcon={<SettingsIcon />}
              onClick={() => setActiveTab('settings')}
            >
              Settings
            </Button>
            <Button 
              variant="outlined" 
              startIcon={<PreviewIcon />}
              onClick={() => navigate(`/projects/${projectId}`)}
            >
              Preview
            </Button>
          </Box>

          <Divider sx={{ mb: 3 }} />

          {activeTab === 'map' && (
            <Box sx={{ height: '500px', bgcolor: '#f0f0f0', borderRadius: 1, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
              <Typography variant="h6" color="text.secondary">
                Map View - In a real application, this would display an interactive map using a library like Leaflet or Google Maps
              </Typography>
            </Box>
          )}

          {activeTab === 'markers' && (
            <Box>
              <Typography variant="h6" gutterBottom>
                Marker Categories
              </Typography>
              <Grid container spacing={3}>
                {mapSettings.markerCategories.map((category) => (
                  <Grid item xs={12} sm={6} md={3} key={category.id}>
                    <Card>
                      <CardContent>
                        <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                          <Typography variant="h3" sx={{ mr: 1 }}>{category.icon}</Typography>
                          <Typography variant="h6">{category.name}</Typography>
                        </Box>
                        <Box sx={{ 
                          width: '100%', 
                          height: '20px', 
                          bgcolor: category.color,
                          borderRadius: 1,
                          mb: 1
                        }} />
                        <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                          <Button size="small">Edit</Button>
                          <Button size="small" color="error">Delete</Button>
                        </Box>
                      </CardContent>
                    </Card>
                  </Grid>
                ))}
                <Grid item xs={12} sm={6} md={3}>
                  <Card sx={{ 
                    height: '100%', 
                    display: 'flex', 
                    alignItems: 'center', 
                    justifyContent: 'center',
                    border: `2px dashed ${theme.palette.divider}`
                  }}>
                    <CardContent>
                      <Box sx={{ textAlign: 'center' }}>
                        <IconButton color="primary" sx={{ mb: 1 }}>
                          <AddLocationIcon fontSize="large" />
                        </IconButton>
                        <Typography>Add New Category</Typography>
                      </Box>
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
            </Box>
          )}

          {activeTab === 'settings' && (
            <Box>
              <Typography variant="h6" gutterBottom>
                Map Settings
              </Typography>
              <Grid container spacing={3}>
                <Grid item xs={12} md={6}>
                  <TextField
                    fullWidth
                    label="Initial Zoom Level"
                    name="initialZoom"
                    type="number"
                    value={mapSettings.initialZoom}
                    onChange={handleSettingChange}
                    InputProps={{ inputProps: { min: 1, max: 20 } }}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <FormControl fullWidth>
                    <InputLabel>Base Map Style</InputLabel>
                    <Select
                      value={mapSettings.baseMapStyle}
                      label="Base Map Style"
                      onChange={handleBaseMapChange}
                    >
                      <MenuItem value="streets">Streets</MenuItem>
                      <MenuItem value="satellite">Satellite</MenuItem>
                      <MenuItem value="light">Light</MenuItem>
                      <MenuItem value="dark">Dark</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    fullWidth
                    label="Initial Center Latitude"
                    name="initialCenterLat"
                    type="number"
                    value={mapSettings.initialCenter.lat}
                    onChange={(e) => {
                      const lat = parseFloat(e.target.value);
                      setMapSettings(prev => prev ? {
                        ...prev,
                        initialCenter: { ...prev.initialCenter, lat }
                      } : null);
                    }}
                    InputProps={{ inputProps: { min: -90, max: 90, step: 0.000001 } }}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    fullWidth
                    label="Initial Center Longitude"
                    name="initialCenterLng"
                    type="number"
                    value={mapSettings.initialCenter.lng}
                    onChange={(e) => {
                      const lng = parseFloat(e.target.value);
                      setMapSettings(prev => prev ? {
                        ...prev,
                        initialCenter: { ...prev.initialCenter, lng }
                      } : null);
                    }}
                    InputProps={{ inputProps: { min: -180, max: 180, step: 0.000001 } }}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
                    Interaction Settings
                  </Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Box sx={{ display: 'flex', alignItems: 'center' }}>
                    <Button
                      variant={mapSettings.allowComments ? "contained" : "outlined"}
                      onClick={() => handleToggleChange('allowComments')}
                      sx={{ mr: 2 }}
                    >
                      {mapSettings.allowComments ? "Comments Enabled" : "Comments Disabled"}
                    </Button>
                    <Typography variant="body2">
                      Allow users to leave comments on markers
                    </Typography>
                  </Box>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Box sx={{ display: 'flex', alignItems: 'center' }}>
                    <Button
                      variant={mapSettings.allowVoting ? "contained" : "outlined"}
                      onClick={() => handleToggleChange('allowVoting')}
                      sx={{ mr: 2 }}
                    >
                      {mapSettings.allowVoting ? "Voting Enabled" : "Voting Disabled"}
                    </Button>
                    <Typography variant="body2">
                      Allow users to upvote/downvote markers
                    </Typography>
                  </Box>
                </Grid>
              </Grid>
            </Box>
          )}
        </Paper>
      </Container>

      <Snackbar 
        open={snackbar.open} 
        autoHideDuration={6000} 
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={handleCloseSnackbar} severity={snackbar.severity}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default MapEditorPage; 