import React, { useState } from 'react';
import {
  Container,
  Typography,
  Box,
  Paper,
  Button,
  TextField,
  Grid,
  Card,
  CardContent,
  CardActions,
  Divider,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  SelectChangeEvent,
  Snackbar,
  Alert,
  useTheme,
  Tabs,
  Tab,
  AppBar,
  Toolbar,
  IconButton,
  Avatar
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import MapIcon from '@mui/icons-material/Map';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import DashboardIcon from '@mui/icons-material/Dashboard';
import CommentIcon from '@mui/icons-material/Comment';
import AssessmentIcon from '@mui/icons-material/Assessment';
import SmartToyIcon from '@mui/icons-material/SmartToy';
import SettingsIcon from '@mui/icons-material/Settings';

// Import admin components
import { 
  AdminDashboard, 
  CommentModeration, 
  MapGallery, 
  Reports,
  AISettings
} from '../components/admin';

// Mock data for existing projects
const mockProjects = [
  {
    id: 1,
    title: 'Downtown Revitalization',
    description: 'Community input for downtown area improvements',
    type: 'map',
    createdAt: '2023-02-15',
    status: 'active'
  },
  {
    id: 2,
    title: 'Neighborhood Park Redesign',
    description: 'Gathering feedback on park amenities and layout',
    type: 'map',
    createdAt: '2023-03-01',
    status: 'active'
  }
];

// Interface for tab panel props
interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

// Tab Panel component
function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`admin-tabpanel-${index}`}
      aria-labelledby={`admin-tab-${index}`}
      {...other}
      style={{ height: 'calc(100vh - 160px)', overflowY: 'auto' }}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

// Helper function for a11y props
function a11yProps(index: number) {
  return {
    id: `admin-tab-${index}`,
    'aria-controls': `admin-tabpanel-${index}`,
  };
}

const AdminPage = () => {
  const theme = useTheme();
  const [projects, setProjects] = useState(mockProjects);
  const [openNewProject, setOpenNewProject] = useState(false);
  const [newProject, setNewProject] = useState({
    title: '',
    description: '',
    type: 'map'
  });
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success' as 'success' | 'error' | 'info' | 'warning'
  });
  
  // State for tab management
  const [tabValue, setTabValue] = useState(0);

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setNewProject(prev => ({ ...prev, [name]: value }));
  };

  const handleTypeChange = (e: SelectChangeEvent) => {
    setNewProject(prev => ({ ...prev, type: e.target.value }));
  };

  const handleCreateProject = () => {
    if (!newProject.title || !newProject.description) {
      setSnackbar({
        open: true,
        message: 'Please fill in all required fields',
        severity: 'error'
      });
      return;
    }

    const newId = projects.length > 0 ? Math.max(...projects.map(p => p.id)) + 1 : 1;
    const createdProject = {
      ...newProject,
      id: newId,
      createdAt: new Date().toISOString().split('T')[0],
      status: 'active'
    };

    setProjects([...projects, createdProject]);
    setNewProject({ title: '', description: '', type: 'map' });
    setOpenNewProject(false);
    setSnackbar({
      open: true,
      message: 'Project created successfully!',
      severity: 'success'
    });
  };

  const handleDeleteProject = (id: number) => {
    setProjects(projects.filter(project => project.id !== id));
    setSnackbar({
      open: true,
      message: 'Project deleted successfully',
      severity: 'success'
    });
  };

  const handleCloseSnackbar = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  // Projects section component
  const ProjectsSection = () => (
    <>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h5" component="h2">
          Your Projects
        </Typography>
        <Button 
          variant="contained" 
          startIcon={<AddIcon />}
          onClick={() => setOpenNewProject(!openNewProject)}
        >
          {openNewProject ? 'Cancel' : 'Create New Project'}
        </Button>
      </Box>

      {openNewProject && (
        <Paper sx={{ p: 3, mb: 4, bgcolor: theme.palette.background.default }}>
          <Typography variant="h6" gutterBottom>
            Create New Project
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Project Title"
                name="title"
                value={newProject.title}
                onChange={handleInputChange}
                required
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth>
                <InputLabel>Project Type</InputLabel>
                <Select
                  value={newProject.type}
                  label="Project Type"
                  onChange={handleTypeChange}
                >
                  <MenuItem value="map">Community Input Map</MenuItem>
                  <MenuItem value="survey">Survey</MenuItem>
                  <MenuItem value="forum">Discussion Forum</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="Project Description"
                name="description"
                value={newProject.description}
                onChange={handleInputChange}
                multiline
                rows={3}
                required
              />
            </Grid>
            <Grid item xs={12}>
              <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
                <Button 
                  variant="contained" 
                  onClick={handleCreateProject}
                >
                  Create Project
                </Button>
              </Box>
            </Grid>
          </Grid>
        </Paper>
      )}

      <Grid container spacing={3}>
        {projects.map(project => (
          <Grid item xs={12} md={6} key={project.id}>
            <Card>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                  <MapIcon sx={{ mr: 1, color: theme.palette.primary.main }} />
                  <Typography variant="h6">{project.title}</Typography>
                </Box>
                <Typography variant="body2" color="text.secondary" gutterBottom>
                  Created: {project.createdAt}
                </Typography>
                <Typography variant="body1" paragraph>
                  {project.description}
                </Typography>
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  <Typography variant="body2" sx={{ 
                    bgcolor: project.status === 'active' ? 'success.main' : 'warning.main',
                    color: 'white',
                    px: 1,
                    py: 0.5,
                    borderRadius: 1
                  }}>
                    {project.status.toUpperCase()}
                  </Typography>
                </Box>
              </CardContent>
              <Divider />
              <CardActions>
                <Button 
                  size="small" 
                  startIcon={<EditIcon />}
                  href={`/admin/projects/${project.id}/edit`}
                >
                  Edit
                </Button>
                <Button 
                  size="small" 
                  startIcon={<MapIcon />}
                  href={`/admin/projects/${project.id}/map-editor`}
                >
                  Map Editor
                </Button>
                <Button 
                  size="small" 
                  color="error" 
                  startIcon={<DeleteIcon />}
                  onClick={() => handleDeleteProject(project.id)}
                >
                  Delete
                </Button>
              </CardActions>
            </Card>
          </Grid>
        ))}
      </Grid>
    </>
  );

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static" color="default">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Admin Dashboard
          </Typography>
          <IconButton size="large" edge="end" color="inherit">
            <Avatar sx={{ bgcolor: theme.palette.primary.main }}>A</Avatar>
          </IconButton>
        </Toolbar>
        <Tabs 
          value={tabValue} 
          onChange={handleTabChange} 
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="admin tabs"
          sx={{ bgcolor: 'background.paper' }}
        >
          <Tab icon={<DashboardIcon />} label="Dashboard" {...a11yProps(0)} />
          <Tab icon={<MapIcon />} label="Projects" {...a11yProps(1)} />
          <Tab icon={<CommentIcon />} label="Comments" {...a11yProps(2)} />
          <Tab icon={<MapIcon />} label="Map Gallery" {...a11yProps(3)} />
          <Tab icon={<AssessmentIcon />} label="Reports" {...a11yProps(4)} />
          <Tab icon={<SmartToyIcon />} label="AI Settings" {...a11yProps(5)} />
        </Tabs>
      </AppBar>

      <Container maxWidth="lg" sx={{ mt: 2 }}>
        <TabPanel value={tabValue} index={0}>
          <AdminDashboard />
        </TabPanel>
        
        <TabPanel value={tabValue} index={1}>
          <Paper sx={{ p: 3 }}>
            <ProjectsSection />
          </Paper>
        </TabPanel>
        
        <TabPanel value={tabValue} index={2}>
          <CommentModeration />
        </TabPanel>
        
        <TabPanel value={tabValue} index={3}>
          <MapGallery />
        </TabPanel>
        
        <TabPanel value={tabValue} index={4}>
          <Reports />
        </TabPanel>
        
        <TabPanel value={tabValue} index={5}>
          <AISettings />
        </TabPanel>
      </Container>

      <Snackbar 
        open={snackbar.open} 
        autoHideDuration={6000} 
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={handleCloseSnackbar} severity={snackbar.severity}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default AdminPage; 