import React, { useContext, useState } from 'react';
import { 
  AppBar, 
  Toolbar, 
  Typography, 
  Button, 
  IconButton, 
  Box, 
  Container,
  useTheme,
  useMediaQuery,
  Menu,
  MenuItem,
  Divider,
  Avatar,
  Tooltip,
  Badge
} from '@mui/material';
import { ThemeContext } from '../../theme/ThemeContext';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import MenuIcon from '@mui/icons-material/Menu';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import NotificationsIcon from '@mui/icons-material/Notifications';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import { Link } from 'react-router-dom';

const Header = () => {
  const { toggleTheme } = useContext(ThemeContext);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  
  // State for mobile menu
  const [mobileMenuAnchorEl, setMobileMenuAnchorEl] = useState<null | HTMLElement>(null);
  
  // State for tools dropdown
  const [toolsAnchorEl, setToolsAnchorEl] = useState<null | HTMLElement>(null);
  
  // State for user menu
  const [userMenuAnchorEl, setUserMenuAnchorEl] = useState<null | HTMLElement>(null);
  
  // Mock authentication state (in a real app, this would come from an auth context)
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  
  // Toggle mock authentication for demo purposes
  const toggleAuth = () => {
    setIsAuthenticated(!isAuthenticated);
    if (!isAuthenticated) {
      // When logging in, randomly decide if user is admin (for demo)
      setIsAdmin(Math.random() > 0.5);
    } else {
      setIsAdmin(false);
    }
  };
  
  const handleMobileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setMobileMenuAnchorEl(event.currentTarget);
  };
  
  const handleMobileMenuClose = () => {
    setMobileMenuAnchorEl(null);
  };
  
  const handleToolsMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setToolsAnchorEl(event.currentTarget);
  };
  
  const handleToolsMenuClose = () => {
    setToolsAnchorEl(null);
  };
  
  const handleUserMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setUserMenuAnchorEl(event.currentTarget);
  };
  
  const handleUserMenuClose = () => {
    setUserMenuAnchorEl(null);
  };
  
  const mainNavItems = [
    { name: 'Home', path: '/' },
    { name: 'Projects', path: '/projects' },
    { name: 'Community', path: '/community' },
    { name: 'Resources', path: '/resources' }
  ];
  
  const toolsItems = [
    { name: 'Implementation Timeline', path: '/tools/timeline' },
    { name: 'Funding Source Matcher', path: '/tools/funding' },
    { name: 'Regulatory Compliance', path: '/tools/compliance' },
    { name: 'Outcome Measurement', path: '/tools/outcomes' },
    { name: 'Collaborative Design', path: '/tools/design' },
    { name: 'Scenario Testing', path: '/tools/scenarios' }
  ];

  return (
    <AppBar position="static" color="primary" elevation={0}>
      <Container maxWidth="lg">
        <Toolbar disableGutters>
          <Typography
            variant="h6"
            component={Link}
            to="/"
            sx={{
              mr: 2,
              fontWeight: 700,
              color: 'white',
              textDecoration: 'none',
              flexGrow: { xs: 1, md: 0 }
            }}
          >
            PLANNING TOOL
          </Typography>

          {isMobile ? (
            <>
              <IconButton
                size="large"
                edge="end"
                color="inherit"
                aria-label="menu"
                onClick={handleMobileMenuOpen}
              >
                <MenuIcon />
              </IconButton>
              <Menu
                anchorEl={mobileMenuAnchorEl}
                open={Boolean(mobileMenuAnchorEl)}
                onClose={handleMobileMenuClose}
                keepMounted
              >
                {mainNavItems.map((item) => (
                  <MenuItem 
                    key={item.name} 
                    component={Link} 
                    to={item.path}
                    onClick={handleMobileMenuClose}
                  >
                    {item.name}
                  </MenuItem>
                ))}
                <Divider />
                <MenuItem onClick={handleMobileMenuClose}>
                  <Typography variant="subtitle2" color="text.secondary">
                    Planning Tools
                  </Typography>
                </MenuItem>
                {toolsItems.map((item) => (
                  <MenuItem 
                    key={item.name} 
                    component={Link} 
                    to={item.path}
                    onClick={handleMobileMenuClose}
                    sx={{ pl: 3 }}
                  >
                    {item.name}
                  </MenuItem>
                ))}
                <Divider />
                {isAuthenticated ? (
                  <>
                    <MenuItem component={Link} to="/dashboard" onClick={handleMobileMenuClose}>
                      Dashboard
                    </MenuItem>
                    <MenuItem component={Link} to="/profile" onClick={handleMobileMenuClose}>
                      Profile
                    </MenuItem>
                    {isAdmin && (
                      <MenuItem component={Link} to="/admin" onClick={handleMobileMenuClose}>
                        Admin Panel
                      </MenuItem>
                    )}
                    <MenuItem onClick={() => { toggleAuth(); handleMobileMenuClose(); }}>
                      Logout
                    </MenuItem>
                  </>
                ) : (
                  <MenuItem onClick={() => { toggleAuth(); handleMobileMenuClose(); }}>
                    Login / Register
                  </MenuItem>
                )}
                <Divider />
                <MenuItem onClick={() => { toggleTheme(); handleMobileMenuClose(); }}>
                  Toggle Theme
                </MenuItem>
              </Menu>
            </>
          ) : (
            <>
              <Box sx={{ flexGrow: 1, display: 'flex' }}>
                {mainNavItems.map((item) => (
                  <Button
                    key={item.name}
                    component={Link}
                    to={item.path}
                    sx={{ 
                      color: 'white',
                      mx: 1,
                      '&:hover': {
                        backgroundColor: 'rgba(255, 255, 255, 0.1)'
                      }
                    }}
                  >
                    {item.name}
                  </Button>
                ))}
                <Button
                  sx={{ 
                    color: 'white',
                    mx: 1,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.1)'
                    }
                  }}
                  endIcon={<KeyboardArrowDownIcon />}
                  onClick={handleToolsMenuOpen}
                >
                  Tools
                </Button>
                <Menu
                  anchorEl={toolsAnchorEl}
                  open={Boolean(toolsAnchorEl)}
                  onClose={handleToolsMenuClose}
                >
                  {toolsItems.map((item) => (
                    <MenuItem 
                      key={item.name} 
                      component={Link} 
                      to={item.path}
                      onClick={handleToolsMenuClose}
                    >
                      {item.name}
                    </MenuItem>
                  ))}
                </Menu>
              </Box>

              {/* Theme toggle button */}
              <IconButton 
                sx={{ ml: 1 }} 
                onClick={toggleTheme} 
                color="inherit"
                aria-label="toggle theme"
              >
                {theme.palette.mode === 'dark' ? <Brightness7Icon /> : <Brightness4Icon />}
              </IconButton>

              {/* User section */}
              {isAuthenticated ? (
                <>
                  {/* Notifications */}
                  <IconButton color="inherit" sx={{ ml: 1 }}>
                    <Badge badgeContent={3} color="error">
                      <NotificationsIcon />
                    </Badge>
                  </IconButton>
                  
                  {/* Admin icon if user is admin */}
                  {isAdmin && (
                    <Tooltip title="Admin Panel">
                      <IconButton 
                        component={Link} 
                        to="/admin" 
                        color="inherit" 
                        sx={{ ml: 1 }}
                      >
                        <AdminPanelSettingsIcon />
                      </IconButton>
                    </Tooltip>
                  )}
                  
                  {/* User menu */}
                  <Box sx={{ ml: 1 }}>
                    <Tooltip title="Account settings">
                      <IconButton
                        onClick={handleUserMenuOpen}
                        sx={{ p: 0 }}
                      >
                        <Avatar sx={{ bgcolor: theme.palette.secondary.main }}>U</Avatar>
                      </IconButton>
                    </Tooltip>
                    <Menu
                      anchorEl={userMenuAnchorEl}
                      open={Boolean(userMenuAnchorEl)}
                      onClose={handleUserMenuClose}
                    >
                      <MenuItem component={Link} to="/dashboard" onClick={handleUserMenuClose}>
                        Dashboard
                      </MenuItem>
                      <MenuItem component={Link} to="/profile" onClick={handleUserMenuClose}>
                        Profile
                      </MenuItem>
                      <MenuItem component={Link} to="/settings" onClick={handleUserMenuClose}>
                        Settings
                      </MenuItem>
                      <Divider />
                      <MenuItem onClick={() => { toggleAuth(); handleUserMenuClose(); }}>
                        Logout
                      </MenuItem>
                    </Menu>
                  </Box>
                </>
              ) : (
                <Button 
                  color="inherit" 
                  startIcon={<AccountCircleIcon />}
                  onClick={toggleAuth}
                  sx={{ ml: 1 }}
                >
                  Login
                </Button>
              )}
            </>
          )}
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header; 