import React from 'react';
import { 
  Container, 
  Typography, 
  Box, 
  Grid, 
  Card, 
  CardContent, 
  CardMedia, 
  Button,
  Paper,
  useTheme,
  Stack
} from '@mui/material';

// Feature card component
const FeatureCard = ({ title, description, icon }: { title: string; description: string; icon: string }) => {
  const theme = useTheme();
  
  return (
    <Card sx={{ 
      height: '100%', 
      display: 'flex', 
      flexDirection: 'column',
      transition: 'transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out',
      '&:hover': {
        transform: 'translateY(-8px)',
        boxShadow: '0 12px 20px rgba(0, 0, 0, 0.1)'
      }
    }}>
      <CardMedia
        component="div"
        sx={{
          pt: '56.25%',
          backgroundColor: theme.palette.primary.main,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          color: 'white',
          fontSize: '3rem'
        }}
      >
        <Box sx={{ 
          position: 'absolute', 
          top: 0, 
          left: 0, 
          width: '100%', 
          height: '100%', 
          display: 'flex', 
          alignItems: 'center', 
          justifyContent: 'center' 
        }}>
          <Typography variant="h1" component="div" sx={{ fontSize: '3rem' }}>
            {icon}
          </Typography>
        </Box>
      </CardMedia>
      <CardContent sx={{ flexGrow: 1 }}>
        <Typography gutterBottom variant="h5" component="h2">
          {title}
        </Typography>
        <Typography>
          {description}
        </Typography>
      </CardContent>
    </Card>
  );
};

const HomePage = () => {
  const theme = useTheme();

  const features = [
    {
      title: 'Implementation Timeline',
      description: 'Visualize project phases with real-time progress tracking for community members.',
      icon: ''
    },
    {
      title: 'Funding Source Matching',
      description: 'Find potential funding sources based on project characteristics and community priorities.',
      icon: ''
    },
    {
      title: 'Regulatory Compliance',
      description: 'Track necessary permits, environmental reviews, and other regulatory requirements.',
      icon: ''
    },
    {
      title: 'Outcome Measurement',
      description: 'Collect post-implementation data to measure project success against stated goals.',
      icon: ''
    },
    {
      title: 'Collaborative Design',
      description: 'Enable real-time collaboration on design alternatives with commenting and version history.',
      icon: ''
    },
    {
      title: 'Community Engagement',
      description: 'Create multiple scenarios for the same project area and compare community feedback.',
      icon: ''
    }
  ];

  return (
    <Box sx={{ flexGrow: 1 }}>
      {/* Hero Section */}
      <Paper 
        elevation={0}
        sx={{ 
          position: 'relative',
          backgroundColor: theme.palette.mode === 'dark' ? 'rgba(0,0,0,0.7)' : 'rgba(255,255,255,0.7)',
          color: theme.palette.text.primary,
          mb: 4,
          py: 8
        }}
      >
        <Container maxWidth="lg">
          <Box sx={{ textAlign: 'center', mb: 5 }}>
            <Typography
              component="h1"
              variant="h2"
              color="primary"
              gutterBottom
            >
              Community Planning Tool
            </Typography>
            <Typography variant="h5" paragraph>
              Empowering communities through collaborative urban planning
            </Typography>
            <Stack direction="row" spacing={2} justifyContent="center">
              <Button 
                variant="contained" 
                size="large" 
                sx={{ mt: 2 }}
                href="/projects"
              >
                Explore Projects
              </Button>
              <Button 
                variant="outlined" 
                size="large" 
                sx={{ mt: 2 }}
                href="/admin"
              >
                Admin Dashboard
              </Button>
            </Stack>
          </Box>
        </Container>
      </Paper>

      {/* Features Section */}
      <Container maxWidth="lg">
        <Typography variant="h4" component="h2" gutterBottom sx={{ mb: 4, textAlign: 'center' }}>
          Key Features
        </Typography>
        <Grid container spacing={4}>
          {features.map((feature, index) => (
            <Grid item key={index} xs={12} sm={6} md={4}>
              <FeatureCard
                title={feature.title}
                description={feature.description}
                icon={feature.icon}
              />
            </Grid>
          ))}
        </Grid>

        {/* Call to Action */}
        <Box sx={{ textAlign: 'center', mt: 8, mb: 6 }}>
          <Typography variant="h4" component="h2" gutterBottom>
            Ready to start planning?
          </Typography>
          <Stack direction="row" spacing={2} justifyContent="center">
            <Button variant="contained" size="large" sx={{ mt: 2 }} href="/projects">
              Explore Projects
            </Button>
            <Button variant="outlined" size="large" sx={{ mt: 2 }} href="/admin">
              Admin Dashboard
            </Button>
          </Stack>
        </Box>
      </Container>
    </Box>
  );
};

export default HomePage; 