import React from 'react';
import { Box, Container, Typography, Link, Grid, useTheme } from '@mui/material';

const Footer = () => {
  const theme = useTheme();
  const currentYear = new Date().getFullYear();

  return (
    <Box
      component="footer"
      sx={{
        py: 6,
        px: 2,
        mt: 'auto',
        backgroundColor: theme.palette.mode === 'light' 
          ? theme.palette.grey[200] 
          : theme.palette.grey[900]
      }}
    >
      <Container maxWidth="lg">
        <Grid container spacing={4}>
          <Grid item xs={12} sm={4}>
            <Typography variant="h6" color="text.primary" gutterBottom>
              Planning Tool
            </Typography>
            <Typography variant="body2" color="text.secondary">
              Empowering communities through collaborative urban planning.
            </Typography>
          </Grid>
          
          <Grid item xs={12} sm={4}>
            <Typography variant="h6" color="text.primary" gutterBottom>
              Quick Links
            </Typography>
            <Typography variant="body2">
              <Link href="/" color="inherit" sx={{ display: 'block', mb: 1 }}>
                Home
              </Link>
              <Link href="/projects" color="inherit" sx={{ display: 'block', mb: 1 }}>
                Projects
              </Link>
              <Link href="/community" color="inherit" sx={{ display: 'block', mb: 1 }}>
                Community
              </Link>
              <Link href="/about" color="inherit" sx={{ display: 'block', mb: 1 }}>
                About
              </Link>
            </Typography>
          </Grid>
          
          <Grid item xs={12} sm={4}>
            <Typography variant="h6" color="text.primary" gutterBottom>
              Contact Us
            </Typography>
            <Typography variant="body2" color="text.secondary">
              123 Planning Avenue<br />
              Cityville, ST 12345<br />
              info@planningtool.example<br />
              (555) 123-4567
            </Typography>
          </Grid>
        </Grid>
        
        <Box mt={5}>
          <Typography variant="body2" color="text.secondary" align="center">
            {' '}
            {currentYear}
            {' Planning Tool. All rights reserved.'}
          </Typography>
        </Box>
      </Container>
    </Box>
  );
};

export default Footer; 